.container {
  padding-inline: 20px;
}
@media (min-width: 576px) {
  .container {
    padding-inline: 72px;
  }
}
@media (min-width: 1024px) {
  .container {
    max-width: 1020px;
    margin-inline: auto;
    padding-inline: 120px;
  }
}

a {
  text-decoration: none;
  color: inherit;
}

p, h3 {
  margin: 0;
}

.button {
  height: 56px;
  padding: 0 48px;
  border-radius: 40px;
  background: #fff;
  border: none;
  color: #1D1D1D;
  text-align: center;
  font-size: 16px;
  font-weight: 700;
  line-height: 27px;
  cursor: pointer;
  transition: transform 0.3s;
}
.button:hover {
  transform: scale(1.05);
}
.button:hover {
  box-shadow: 1px 4px 16px 0 rgba(232, 232, 240, 0.4);
}
.button:active {
  box-shadow: 1px 4px 16px 0 rgba(232, 232, 240, 0.6392156863);
}

.input {
  font-family: Poppins, sans-serif;
  border-radius: 40px;
  background: #F8F8FA;
  color: #292929;
  font-weight: 400;
  line-height: 20px;
  padding: 14px 0 14px 16px;
}
.input::placeholder {
  font-size: 14px;
  color: #7E7E83;
  font-weight: 400;
}
.input:focus {
  outline: none;
}

.page {
  font-family: Poppins, sans-serif;
  color: #fff;
  font-size: 16px;
  line-height: 22.4px;
  scroll-behavior: smooth;
}
.page__body {
  margin: 0;
  min-width: 320px;
}
.page__menu {
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  opacity: 0;
  pointer-events: none;
  transition: all 0.3s;
  transform: translateX(-100%);
}
.page__menu:target {
  opacity: 1;
  pointer-events: all;
  transform: translateX(0);
}
.page:has(.page__menu:target) {
  overflow: hidden;
}

.header {
  background-image: url("../images/header-bg-mobile.png");
  background-size: cover;
  background-position: 90%;
}
.header__content {
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.header__bottom {
  display: flex;
  justify-content: center;
  padding-bottom: 150px;
}
.header__title {
  color: #fff;
}
@media (min-width: 576px) {
  .header__title {
    font-size: 64px;
    line-height: 64px;
  }
}

.top-bar {
  padding-block: 24px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
@media (min-width: 576px) {
  .top-bar {
    padding-block: 64px;
  }
}
.top-bar__icons {
  display: flex;
  justify-content: flex-end;
  gap: 24px;
}
.top-bar__logo-link {
  display: flex;
}
.top-bar__logo {
  height: 16px;
  transition: transform 0.3s;
}
@media (min-width: 576px) {
  .top-bar__logo {
    height: 24px;
  }
}
.top-bar__logo:hover {
  transform: scale(1.2);
}

.icon {
  display: block;
  height: 24px;
  width: 24px;
  color: #fff;
  background-size: cover;
  transition: transform 0.3s;
}
.icon:hover {
  transform: scale(1.2);
}
@media (min-width: 576px) {
  .icon {
    height: 32px;
    width: 32px;
  }
}
.icon--phone {
  background-image: url("../images/icons/icon-phone.png");
}
.icon--menu {
  background-image: url("../images/icons/icon-burger-menu.png");
}
.icon--menu:hover {
  background-image: url("../images/icons/icon-burger-menu-hover.png");
}
.icon--close {
  background-image: url("../images/icons/icon-close.png");
}

.menu {
  box-sizing: border-box;
  height: 100vh;
  background-color: #C2C2C2;
  font-weight: 600;
  text-transform: uppercase;
  color: #1D1D1D;
  overflow-y: auto;
}
.menu__top {
  margin-bottom: 24px;
}
@media (min-width: 576px) {
  .menu__top {
    margin-bottom: 0;
  }
}
.menu__nav {
  margin-bottom: 32px;
}
.menu__phone-number {
  display: block;
  margin-bottom: 16px;
  color: inherit;
  text-decoration: none;
  font-size: 16px;
  line-height: 27px;
}
.menu__book-test-ride {
  width: fit-content;
  display: block;
  color: inherit;
  text-decoration: none;
  font-size: 12px;
  line-height: 16px;
  letter-spacing: 2px;
  padding-bottom: 7px;
  border-bottom: 1px solid #1D1D1D;
}
.menu__phone-number:hover, .menu__book-test-ride:hover {
  transition: color 0.3s;
}
.menu__phone-number:hover:hover, .menu__book-test-ride:hover:hover {
  color: #fff;
}

.nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-direction: column;
  gap: 32px;
}
.nav__link {
  position: relative;
  color: inherit;
  text-decoration: none;
  font-size: 22px;
  line-height: 22px;
  letter-spacing: 2px;
}
.nav__link::after {
  content: "";
  position: absolute;
  bottom: -8px;
  display: block;
  height: 1px;
  width: 100%;
  background-color: #1D1D1D;
  transition: transform 0.3s;
  transform: scale(0);
  transform-origin: left;
}
.nav__link:hover::after {
  transform: scale(1);
}

.move-free__container {
  --columns: 2;
  display: grid;
  column-gap: 20px;
  grid-template-columns: repeat(var(--columns), 1fr);
}
@media (min-width: 576px) {
  .move-free__container {
    --columns: 6;
    column-gap: 24px;
  }
}
@media (min-width: 1024px) {
  .move-free__container {
    --columns: 12;
  }
}
.move-free__title {
  grid-column: 1/-1;
}
@media (min-width: 576px) {
  .move-free__title {
    grid-column: span 3;
  }
}
@media (min-width: 1024px) {
  .move-free__title {
    grid-column: span 5;
  }
}
.move-free__description {
  margin: 0;
  grid-column: 1/-1;
}
@media (min-width: 576px) {
  .move-free__description {
    grid-column: span 3;
  }
}
@media (min-width: 1024px) {
  .move-free__description {
    grid-column: span 6;
  }
}

.main {
  background-color: #1D1D1D;
}
.main__content {
  display: grid;
  row-gap: 92px;
  padding-block: 92px;
}
@media (min-width: 576px) {
  .main__content {
    row-gap: 120px;
    padding-block: 120px;
  }
}
@media (min-width: 576px) {
  .main__content {
    row-gap: 144px;
    padding-block: 144px;
  }
}

.section-title {
  margin: 0;
  margin-bottom: 48px;
  font-size: 32px;
  font-weight: 500;
  line-height: 32px;
  letter-spacing: -1.5px;
  text-align: center;
}
@media (min-width: 576px) {
  .section-title {
    margin-bottom: 64px;
    font-size: 40px;
  }
}
@media (min-width: 1024px) {
  .section-title {
    font-size: 48px;
  }
}
.section-title--align--left {
  text-align: left;
}

.recommended__products {
  --columns: 2;
  display: grid;
  column-gap: 20px;
  grid-template-columns: repeat(var(--columns), 1fr);
  row-gap: 48px;
}
@media (min-width: 576px) {
  .recommended__products {
    --columns: 6;
    column-gap: 24px;
  }
}
@media (min-width: 1024px) {
  .recommended__products {
    --columns: 12;
  }
}
.recommended__product {
  grid-column: 1/-1;
}
@media (min-width: 576px) {
  .recommended__product {
    grid-column: 2/-2;
  }
}
@media (min-width: 1024px) {
  .recommended__product {
    grid-column: span 4;
  }
}

.product__photo {
  width: 100%;
}
.product__photo:hover {
  cursor: pointer;
  transition: transform 0.3s;
}
.product__photo:hover:hover {
  transform: scale(1.05);
}

.details__wrapper {
  display: grid;
  row-gap: 40px;
}
@media (min-width: 576px) {
  .details__wrapper {
    row-gap: 48px;
  }
}

.detail__photos {
  --columns: 2;
  display: grid;
  column-gap: 20px;
  grid-template-columns: repeat(var(--columns), 1fr);
  margin-bottom: 16px;
}
@media (min-width: 576px) {
  .detail__photos {
    --columns: 6;
    column-gap: 24px;
  }
}
@media (min-width: 1024px) {
  .detail__photos {
    --columns: 12;
  }
}
.detail__photo {
  position: absolute;
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.detail__photo:hover {
  cursor: pointer;
  transition: transform 0.3s;
}
.detail__photo:hover:hover {
  transform: scale(1.05);
}
.detail__link {
  position: relative;
}
@media (min-width: 576px) {
  .detail__link--wide {
    grid-column: span 4;
  }
}
@media (min-width: 1024px) {
  .detail__link--wide {
    grid-column: span 8;
  }
}
.detail__link--square {
  height: 0;
  padding-bottom: 100%;
}
@media (min-width: 576px) {
  .detail__link--square {
    grid-column: span 2;
  }
}
@media (min-width: 1024px) {
  .detail__link--square {
    grid-column: span 4;
  }
}
.detail__title {
  margin: 0;
  font-weight: 700;
  font-size: 24px;
  line-height: 33.6px;
}
.detail__container {
  --columns: 2;
  display: grid;
  column-gap: 20px;
  grid-template-columns: repeat(var(--columns), 1fr);
}
@media (min-width: 576px) {
  .detail__container {
    --columns: 6;
    column-gap: 24px;
  }
}
@media (min-width: 1024px) {
  .detail__container {
    --columns: 12;
  }
}
.detail__button {
  display: grid;
  justify-content: center;
  align-items: center;
  grid-column: 1/-1;
}
@media (min-width: 576px) {
  .detail__button {
    grid-column: 2/6;
  }
}
@media (min-width: 1024px) {
  .detail__button {
    grid-column: 5/9;
  }
}
.detail__button:hover {
  box-shadow: 1px 4px 16px 0 rgba(232, 232, 240, 0.4);
}
.detail__button:active {
  box-shadow: 1px 4px 16px 0 rgba(232, 232, 240, 0.6392156863);
}

.textarea {
  font-family: Poppins, sans-serif;
  height: 148px;
  border-radius: 16px;
  background: #F8F8FA;
  color: #292929;
  font-weight: 400;
  line-height: 20px;
  padding: 14px 16px;
  resize: none;
}
.textarea::placeholder {
  font-size: 14px;
  color: #7E7E83;
  font-weight: 400;
}
.textarea:focus {
  outline: none;
}

.form {
  display: grid;
  row-gap: 16px;
}
.form__button {
  margin-top: 32px;
}

@media (min-width: 576px) {
  .contact-us__wrapper {
    --columns: 2;
    display: grid;
    column-gap: 20px;
    grid-template-columns: repeat(var(--columns), 1fr);
  }
}
@media (min-width: 576px) and (min-width: 576px) {
  .contact-us__wrapper {
    --columns: 6;
    column-gap: 24px;
  }
}
@media (min-width: 576px) and (min-width: 1024px) {
  .contact-us__wrapper {
    --columns: 12;
  }
}
.contact-us__form {
  margin-bottom: 48px;
}
@media (min-width: 576px) {
  .contact-us__form {
    grid-column: 1/4;
  }
}
@media (min-width: 1024px) {
  .contact-us__form {
    grid-column: 1/6;
  }
}
@media (min-width: 576px) {
  .contact-us__info {
    margin: 0;
    grid-column: 4/6;
  }
}
@media (min-width: 1024px) {
  .contact-us__info {
    grid-column: 8/11;
  }
}
.contact-us__label {
  margin-bottom: 8px;
  font-size: 14px;
  font-weight: 400;
  line-height: 140%;
}
.contact-us__link {
  display: block;
  color: #fff;
  font-size: 16px;
  font-weight: 500;
  line-height: 140%;
}
.contact-us__link:not(:last-child) {
  margin-bottom: 24px;
}
.contact-us__link:hover {
  transition: color 0.3s;
}
.contact-us__link:hover:hover {
  color: #C2C2C2;
}

.footer {
  height: 200px;
  width: 100%;
  background: url("./../images/footer.png");
  background-size: cover;
  background-position: 80%;
}
@media (min-width: 576px) {
  .footer {
    height: 480px;
  }
}
@media (min-width: 1024px) {
  .footer {
    height: 555px;
  }
}